import React, {Component} from 'react';
import { get as _get } from 'lodash';

/* eslint-disable */

class Contact extends Component {
  render() {

    const name = _get(this.props,"data.name","");
    const street = _get(this.props,"data.address.street","");
    const city = _get(this.props,"data.address.city","");
    const state = _get(this.props,"data.address.state","");
    const zip = _get(this.props,"data.address.zip","");
    const phone = _get(this.props,"data.phone","");
    const email = _get(this.props,"data.email","");
    const message = _get(this.props,"data.contactmessage","");
 
    return (
      <section id="contact">

        <div className="row section-head">

          <div className="two columns header-col">

            <h1>
              <span>Get In Touch.</span>
            </h1>

          </div>

          <div className="ten columns">

            <p className="lead">{message}</p>

          </div>

        </div>

        <div className="row">
          <div className="eight columns">

            <form action="" method="post" id="contactForm" name="contactForm">
              <fieldset>

                <div>
                  <label htmlFor="contactName">Name
                    <span className="required">*</span>
                  </label>
                  <input
                    type="text"
                    defaultValue=""
                    size="35"
                    id="contactName"
                    name="contactName"
                    autoComplete="off"
                    onChange={this.handleChange}/>
                </div>

                <div>
                  <label htmlFor="contactEmail">Email
                    <span className="required">*</span>
                  </label>
                  <input
                    type="text"
                    defaultValue=""
                    size="35"
                    id="contactEmail"
                    name="contactEmail"
                    onChange={this.handleChange}/>
                </div>

                <div>
                  <label htmlFor="contactSubject">Subject</label>
                  <input
                    type="text"
                    defaultValue=""
                    size="35"
                    id="contactSubject"
                    name="contactSubject"
                    onChange={this.handleChange}/>
                </div>

                <div>
                  <label htmlFor="contactMessage">Message
                    <span className="required">*</span>
                  </label>
                  <textarea cols="50" rows="15" id="contactMessage" name="contactMessage"></textarea>
                </div>

                <div>
                  <button className="submit">Submit</button>
                  <span id="image-loader">
                    <img alt="" src="images/loader.gif"/>
                  </span>
                </div>
              </fieldset>
            </form>

            <div id="message-warning">
              Error boy</div>
            <div id="message-success">
              <i className="fa fa-check"></i>Your message was sent, thank you!<br/>
            </div>
          </div>

          <aside className="four columns footer-widgets">
            <div className="widget widget_contact">

              <h4><i className="fa fa-map-marker"></i> Address</h4>
              <div className="address">
                <span>{name}</span>
                <span>{street}</span>
                <span>{state}</span>
                <span>{city}</span>
                <span>{zip}</span>
                <span><i className="fa fa-phone fa-fw"></i> <b>{phone}</b></span>
              </div>
            </div>

            {/* <div className="widget widget_tweets">
              <h4 className="widget-title">Latest Tweets</h4>
              <ul id="twitter">
                <li>
                  <span>
                    This is Photoshop's version of Lorem Ipsum. Proin gravida nibh vel velit auctor
                    aliquet. Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat
                    ipsum
                    <a href="#">http://t.co/CGIrdxIlI3</a>
                  </span>
                  <b>
                    <a href="#">2 Days Ago</a>
                  </b>
                </li>
                <li>
                  <span>
                    Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium
                    doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore
                    veritatis et quasi
                    <a href="#">http://t.co/CGIrdxIlI3</a>
                  </span>
                  <b>
                    <a href="#">3 Days Ago</a>
                  </b>
                </li>
              </ul>
            </div> */}
          </aside>
        </div>
      </section>
    );
  }
}

export default Contact;



